import { GTMCategory } from '../../models/google-tag-manager/gtm-category';
import { GTMEvent } from '../../models/google-tag-manager/gtm-event';
import { GTMEventObject } from '../../models/google-tag-manager/gtm-event-object';
import { ExtendedWindow } from '../../models/extended-window.model';
import { GTMVariables } from '../../models/gtm-variables';
import { GTMECommerceEvent } from '../../models/google-tag-manager/gtm-ecommerce-event';
import { GTMEventBase } from '../../models/google-tag-manager/gtm-event-base';

declare const window: ExtendedWindow;

const GTMDefaultCallbackTimeout = 2000;

export const emptyLabel = '-';

/**
 * Google Tag Manager (GTM) service that integrates data on GTM and Google Analytics tools
 */
export class GoogleTagManagerService {
    private static instance: GoogleTagManagerService;

    /**
     * The Singleton's constructor should always be private to prevent direct
     * construction calls with the `new` operator.
     */
    private constructor() { }

    /**
     * The static method that controls the access to the singleton instance.
     *
     * This implementation let you subclass the Singleton class while keeping
     * just one instance of each subclass around.
     */
    public static getInstance(): GoogleTagManagerService {
        if (!GoogleTagManagerService.instance) {
            GoogleTagManagerService.instance = new GoogleTagManagerService();
        }

        return GoogleTagManagerService.instance;
    }

    get gtmVariables(): GTMVariables {
        return (window.VA || {}).GtmVariables || {};
    }

    /**
     * Pushes an event to GTM
     *
     * @param event - Event name
     * @param category - Category name
     * @param action - Action name
     * @param label - Label name
     * @param callback - Callback function invoked after the event has been pushed to GTM
     */
    pushEvent(event: GTMEvent , category: GTMCategory, action: string, label: string, callback: Function = null) {
        if (event && category && action && label) {
            const gtmEvent: GTMEventObject = {
                event: event,
                category: category,
                action,
                label
            };

            if (callback) {
                this.configureGtmCallback(gtmEvent, callback);
            }

            window.dataLayer.push(gtmEvent);
        }
    }

    /**
     * Pushes content views event
     *
     * @param text - Content text
     * @param url - Content url
     */
    pushContentViewsEvent(text: string, url: string) {
        this.pushEvent(GTMEvent.ContentViews, GTMCategory.ContentEngagement, `Content views: ${text}`, url);
    }

    /**
     * Push an agent search
     *
     * @param locality - locality searched by the user
     */
    pushAgentSearch(locality: string) {
        this.pushEvent(GTMEvent.AgentSearch, GTMCategory.ClientEnquiries, 'Agent search', locality);
    }

    /**
     * Pushes content call to action
     *
     * @param text - Content text
     * @param url - Content url
     */
    pushContentCta(text: string, url: string) {
        this.pushEvent(GTMEvent.ContentCtaClicks, GTMCategory.ContentEngagement, `CTA clicks: ${text}`, url);
    }

    /**
     * Pushes leads checkout event
     *
     * @param checkoutStep - Checkout step text
     */
    pushLeadsCheckoutEvent(checkoutStep: string): void {
        this.pushEvent(GTMEvent.LeadCreation, GTMCategory.Leads, `Step: ${checkoutStep}`, this.gtmVariables.product);
    }

    /**
     * Pushes leads checkout complete event
     */
    pushLeadsCheckoutCompleteEvent(): void {
        this.pushLeadsCheckoutEvent('Complete');
    }

    /**
     * Pushes leads request for an offer event
     *
     * @param callback - Callback function invoked after the event has been pushed to GTM
     */
    pushLeadsRequestOfferEvent(callback?: Function): void {
        this.pushEvent(GTMEvent.OfferRequests, GTMCategory.Leads, 'Offer request', this.gtmVariables.product, callback);
    }

    /**
     * Pushes ecommerce transaction event
     * https://support.google.com/tagmanager/answer/6107169?hl=en
     *
     * @param ecommerceEvent - ECommerce transaction event information
     * @param callback - Callback function invoked after the event has been pushed to GTM
     */
    pushECommerceEvent(ecommerceEvent: GTMECommerceEvent, callback: Function): void {
        ecommerceEvent.event = ecommerceEvent.event || GTMEvent.ECommerce;
        ecommerceEvent.currencyCode = ecommerceEvent.currencyCode || 'CH';
        ecommerceEvent.transactionAffiliation = ecommerceEvent.transactionAffiliation || 'Vaudoise.ch';
        ecommerceEvent.transactionProducts = ecommerceEvent.transactionProducts || [];

        this.configureGtmCallback(ecommerceEvent, callback);
        window.dataLayer.push(ecommerceEvent);
    }

    /**
     * Configures GTM callback function so that can be called right after GTM event is pushed to the server
     *
     * @param  event - GTM event
     * @param callback - Callback function
     */
    configureGtmCallback(event: GTMEventBase, callback: Function) {
        if (callback) {
            event.eventCallback = callback;
            event.eventTimeout = GTMDefaultCallbackTimeout;
        }
    }
}
