import { GoogleTagManagerService } from './google-tag-manager-service';
import { GTMCategory } from '../../models/google-tag-manager/gtm-category';
import { GTMEvent } from '../../models/google-tag-manager/gtm-event';

/**
 * Track Logo click events
 */
export function trackLogo(): void {
    const element = document.querySelector<HTMLElement>('.cms-header__logo');

    // In case we're in a layout without any header (like ecvm)
    if (element) {
        element.addEventListener('click', () => {
            GoogleTagManagerService.getInstance().pushEvent(GTMEvent.Logo, GTMCategory.Navigation, 'Logo clicks', '-');
        });
    }
}

/**
* Tracks Language Selection events
*/
export function trackLanguageSelection(): void {
    const dataLanguageAttribute = 'data-lang';
    const languageSelector = document.querySelector<HTMLElement>('.top-nav__item--language');

    if (languageSelector) {

        const languageContainer = languageSelector.querySelectorAll<HTMLAnchorElement>(`[${dataLanguageAttribute}]`);

        languageContainer.forEach(element => {
            element.addEventListener('click', () =>{
                const language = element.getAttribute(dataLanguageAttribute);
                if (language) {
                    GoogleTagManagerService.getInstance().pushEvent(GTMEvent.LanguageSelection, GTMCategory.Navigation, 'Select language', language);
                }
            });
        });
    }
}


/**
 * Tracks header events
 */
export function trackHeaderEvents(): void {
    trackLogo();
    trackLanguageSelection();
}
