import { GoogleTagManagerService } from './google-tag-manager-service';
import { GTMCategory } from '../../models/google-tag-manager/gtm-category';
import { GTMEvent } from '../../models/google-tag-manager/gtm-event';

/**
 * Track page category events
 */
export function trackPageCategoryEvents(): void {
    const elements = document.querySelectorAll<HTMLAnchorElement>('.js-gtm-navigation');

    elements.forEach(element => element.addEventListener(
        'click',
        (evt: Event) => {
            const anchor = evt.currentTarget as HTMLAnchorElement;
            const label = anchor.dataset.gtmLabel || anchor.innerText;

            GoogleTagManagerService.getInstance().pushEvent(GTMEvent.PageCategorySelection, GTMCategory.Navigation, 'Select category', label);
        }
    ));
}

/**
 * Track sub category events
 */
export function trackSubcategoryEvents(): void {
    const elements = document.querySelectorAll<HTMLAnchorElement>('.main-nav__item[data-level="1"] > .main-nav__link');

    elements.forEach(element => element.addEventListener(
        'click',
        (evt: Event) => {
            const anchor = evt.currentTarget as HTMLAnchorElement;
            const label = anchor.dataset.gtmLabel || anchor.innerText;

            GoogleTagManagerService.getInstance().pushEvent(GTMEvent.PageSubcategorySelection, GTMCategory.Navigation, 'Select subcategory', label);
        }
    ));
}

/**
 * Tracks navigations events
 */
export function trackNavigationEvents(): void {
    trackPageCategoryEvents();
    trackSubcategoryEvents();
}
