/**
 * The class name of the toggle button used to show/hide the dropdown
 */
export const toggleClassName = 'js-dropdown-toggle';

/**
 * The class name added to the dropdown content to manage its visibility
 */
export const dropdownContentVisibilityClassName = 'is-shown';

/**
 * Function to toggle the visibility of all dropdown content
 * @export
 */
export function initToggles(): void {
    /**
     * List of dropdowns currently opened
     */
    let openedDropdowns: Array<HTMLElement> = [];

    document.addEventListener('click', (evt: Event) => {
        let targetDropdownElement = getTargetDropdownElement(evt);
        let isTargetDropdownOpened = targetDropdownElement?.classList.contains(dropdownContentVisibilityClassName);

        closeDropdowns(openedDropdowns);

        // If target toggle element is found and it's not visible, 
        // display it and add it to the opened dropdowns array
        if (targetDropdownElement && !isTargetDropdownOpened) {
            targetDropdownElement.classList.add(dropdownContentVisibilityClassName);
            openedDropdowns.push(targetDropdownElement);
        }
    }, false);
}

/**
 * Searches the event target element tree path for the toggle target element.
 * @param evt attached document click event
 */
function getTargetDropdownElement(evt: Event): HTMLElement {
    let targetDropdownElement = undefined;

    for (let target = evt.target as HTMLElement; target && target !== this; target = target.parentNode as HTMLElement) {
        // loop parent nodes from the target to the delegation node
        if (target.classList?.contains(toggleClassName)) {
            const targetSelector = target.getAttribute('aria-controls');

            if (targetSelector) {
                targetDropdownElement = document.getElementById(targetSelector);
                break;
            }
        }
    }

    return targetDropdownElement;
}

/**
 * Close the specified dropdowns by removing the content visibility class.
 * @param openedDropdowns dropdowns to close
 */
function closeDropdowns(openedDropdowns: Array<HTMLElement>): void {
    for (const openDropdown of openedDropdowns) {
        if (openDropdown) {
            openDropdown.classList.remove(dropdownContentVisibilityClassName);
        }
    }
    openedDropdowns.length = 0;
}