/**
 * Retrieve all parents that matches a particular selector
 * from a start element.
 *
 * Will return an empty array if there is no matching elements.
 *
 * @param element the element where to start
 * @param selector the selector to retrieve
 * @returns The selected elements
 */
export function parentsOf(element: Element, selector: string): Array<Element> {
    const elements = [];

    if (typeof selector !== 'undefined') {
        while ((element = element.parentElement) !== null) {
            if (element.nodeType !== Node.ELEMENT_NODE) {
                continue;
            }

            if (element.matches(selector)) {
                elements.push(element);
            }
        }
    }

    return elements;
}

/**
 * Gets all direct children of an element that matches a particular class name
 * @param element the element where to start
 * @param className The class name to match
 * @returns The selected elements
 */
export function directChildrenOf(element: Element, className: string): Array<Element> {
    return (Array.from(element.childNodes) as Array<Element>).filter(child => child.classList && child.classList.contains(className));
}

/**
 * Gets first direct child of an element that matches a particular class name
 *
 * @param element the element where to start
 * @param className The class name to match
 * @returns The selected element
 */
export function firstChildOf(element: Element, className: string): Element {
    const children = directChildrenOf(element, className);

    return children && children.length > 0 ? children[0] : null;
}

/**
 * Method to ensure (try to get, and generate one if doesn't exist) the given element has a name attribute
 *
 * @param parentName - the parent prefix name to be used in case of id generation
 * @param element - the element that contains the validation message
 * @returns The element id
 */
export function ensureElementHasName(parentName: string, element: HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement): string {
    if (!element.name) {
        const randomString = Math.random().toString(36).substring(2);
        const namePrefix = parentName ? `${parentName}-` : '';
        element.name = `${namePrefix}${randomString}`;
    }

    return element.name;
}
