import { firstChildOf, directChildrenOf } from '../../helpers/selectors';
import { classNames } from '../../constants/class-names';
import { Breakpoints } from '../../constants/breakpoints';

/**
 * The class name of the toggle button that show the sub menu
 */
export const navToggleTriggerClass = 'cms-header__trigger';

/**
 * The id of the navigation menu that is shown/hidden
 * on mobile
 */
export const navId = 'main-nav';

export const headerBlock = 'cms-header';

const bemMainNav = 'main-nav';
const bemNavSubmenuClass = 'main-nav__submenu';
const bemMainNavLinkClass = 'main-nav__link';
const bemMainNavLinkDesktopClass = 'main-nav__link--desktop-only';
const bemMainNavItemClass = 'main-nav__item';
const jsNavMenuClass = 'js-nav-menu';
const jsHeaderActiveNode = 'js-header-active-root-node';

const bemNavCategoryContainerClass = 'main-nav__submenu--category-container';

const mainNavSelector = `.${bemMainNav}`;

/**
 * This function activate the hamburger menu which display the nav on mobile
 * The nav is not present on each page (ex: capsule's landing-page)
 */
export function initHeaderNav(): void {
    document.addEventListener('scroll', onScroll, { passive: true });
    document.getElementById(navId)?.addEventListener('click', toggleNavItem);
    document.querySelector(`.${navToggleTriggerClass}`)?.addEventListener('click', toggleMobileMenu, false);
    window.addEventListener('resize', onWindowResize);
}

/**
 * Clears the hidden state for mobile devices
 * @param mainNav Highest level main-nav element
 */
function clearMobileIsHiddenState(mainNav: HTMLElement): void {
    mainNav.querySelectorAll(`.${classNames.isHiddenSm}`).forEach(
        (elem: HTMLElement) => elem.classList.remove(classNames.isHiddenSm)
    );
}

/**
 * Toggles the state of the navigation links for mobile devices
 * @param nav Current navigation menu
 * @param navItem Current navigation item
 * @param hideLinks Whether should hide or show siblings and parent navigation links
 */
function manageNavItemsIsHiddenState(nav: HTMLElement, navItem: HTMLElement, hideLinks: boolean): void {
    // Go through sibling elements of clicked nav item and hide or show then
    directChildrenOf(nav, bemMainNavItemClass).forEach((elem: HTMLElement) => {
        if (navItem !== elem) {
            const navLink = firstChildOf(elem, bemMainNavLinkClass);
            navLink.classList.toggle(classNames.isHiddenSm, hideLinks);
        }
    });

    // Move to parent nav item
    const parentNavItem = navItem
        .closest(`.${bemMainNav}`)
        .closest(`.${bemMainNavItemClass}`) as HTMLElement;

    if (parentNavItem) {
        // Get parent nav link
        const parentNavLink = firstChildOf(parentNavItem, bemMainNavLinkClass);
        if (parentNavLink) {
            const isNowHidden = parentNavLink.classList.toggle(classNames.isHiddenSm, hideLinks);

            // Recursively apply those rules to parent elements
            manageNavItemsIsHiddenState(
                parentNavItem.closest(`.${bemMainNav}`),
                parentNavItem,
                // As the ||| Menu is not shown on mobile,
                // its submenu items are shown with the highest level items
                // So the parent level items are considered as siblings
                parentNavLink.classList.contains(bemMainNavLinkDesktopClass) ? isNowHidden : true);
        }
    }
}

/**
 * The behavior to run on click on the navigation link
 * @param evt - the MouseClickEvent on the link
 */
function toggleNavItem(evt: Event): void {
    let navLink = evt.target as HTMLAnchorElement;

    if (!navLink.classList.contains(bemMainNavLinkClass)) {
        navLink = navLink.closest(`.${bemMainNavLinkClass}`);
    }

    if (navLink
        && navLink.classList.contains(bemMainNavLinkClass)
        && navLink.nextElementSibling) {
        const navItem = navLink.closest(`.${bemMainNavItemClass}`) as HTMLElement;

        if (navItem) {
            const nav = navItem.closest(`.${bemMainNav}`) as HTMLElement;

            if (nav) {
                const mainNav = evt.currentTarget as HTMLElement;

                clearMobileIsHiddenState(mainNav);

                if (!navItem.classList.contains(jsNavMenuClass)) {
                    // As the ||| Menu is not shown on mobile,
                    // its submenu items are shown with the highiest level items
                    // So we don't apply the is-hidden-sm management for it.
                    const openNavItem = firstChildOf(nav, classNames.isOpen);
                    manageNavItemsIsHiddenState(nav, navItem, navItem !== openNavItem);
                }
                manageNavItemsIsOpenState(nav, navItem);

                syncDesktopAndMobileMenuStates(mainNav);
                recalculateCategoryContainerHeight(mainNav);

                if (navLink.getAttribute('href') === '#') {
                    evt.preventDefault();
                }

                document.removeEventListener('click', windowClickCloseMenu);

                if (mainNav.classList.contains(classNames.isOpen)) {
                    document.addEventListener('click', windowClickCloseMenu);
                }
            }
        }
    }
}

/**
 * Handles windows clicks so that main menu is closed if click was performed outside of the main menu container element
 * @param evt Window click event
 */
function windowClickCloseMenu(evt: Event): void {
    const target = evt.target as HTMLElement;

    let mainNav = target.closest<HTMLElement>(mainNavSelector);

    if (!mainNav && !target.classList.contains(navToggleTriggerClass)) {
        mainNav = document.querySelector<HTMLElement>(mainNavSelector);

        if (mainNav !== null) {
            closeMenu(mainNav);
            clearMobileIsHiddenState(mainNav);
            document.querySelector(`.${navToggleTriggerClass}`)?.classList?.remove(classNames.isActive);
            document.removeEventListener('click', windowClickCloseMenu);
        }
    }
}

/**
 * Toggle the state of the clicked navigation item and clear it on the other items
 * @param nav Navigation menu that wraps clicked nav item
 * @param navItem Clicked navigation menu
 */
function manageNavItemsIsOpenState(nav: HTMLElement, navItem: HTMLElement): void {
    nav.querySelectorAll(`.${bemMainNavItemClass}`).forEach((elem: HTMLElement) => {
        if (navItem !== elem) {
            elem.classList.remove(classNames.isOpen);
            elem.querySelector(`.${bemNavSubmenuClass}`)?.classList.remove(classNames.isOpen);
        }
    });


    let selected = navItem.querySelector(`.${jsHeaderActiveNode}`)

    if (selected) {
        selected.classList.toggle(classNames.isOpen);
        selected.querySelector(`.${bemNavSubmenuClass}`)?.classList.toggle(classNames.isOpen);
    }


    navItem.classList.toggle(classNames.isOpen);
    navItem.querySelector(`.${bemNavSubmenuClass}`)?.classList.toggle(classNames.isOpen);
}

/**
 * Synchronizes menu items under "||| Menu" and state of menu on mobile
 * @param mainNav Highiest level main-nav element
 */
function syncDesktopAndMobileMenuStates(mainNav: HTMLElement): void {
    let hasOpenItems = mainNav.querySelectorAll(`.${classNames.isOpen}`).length > 0;

    // Open all items under "||| Menu" if there's no other menu opened and we are on mobile devices
    if (!hasOpenItems && window.innerWidth < Breakpoints.md && mainNav.classList.contains(classNames.isOpen)) {
        openHamburgerMenuItems(mainNav);

        hasOpenItems = true;
    }

    // Sync toggle button and menu
    mainNav.classList.toggle(classNames.isOpen, hasOpenItems);
    document.querySelector(`.${navToggleTriggerClass}`).classList.toggle(classNames.isActive, hasOpenItems);
}

/**
 * Re-calculate category container height dynamically so that child items don't overflow outside the container area
 * @param mainNav Highest level main-nav element
 * @returns {void}
 */
function recalculateCategoryContainerHeight(mainNav: HTMLElement) {
    if (!mainNav) {
        return;
    }

    const categoryContainer = mainNav.querySelector(`.${bemNavCategoryContainerClass}.${classNames.isOpen}`) as HTMLElement;

    if (!categoryContainer) {
        return;
    }

    categoryContainer.style.height = "auto";

    if (window.innerWidth >= Breakpoints.md) {
        const openCategories = categoryContainer.querySelectorAll(`.${bemNavSubmenuClass}.${classNames.isOpen}`);

        if (openCategories.length > 0) {
            let maxHeight = -1;

            for (let i = 0; i < openCategories.length; ++i) {
                const openCategory = openCategories[i];

                if (openCategory.clientHeight > maxHeight) {
                    maxHeight = openCategory.clientHeight;
                }
            }

            const style = window.getComputedStyle(categoryContainer);

            maxHeight = maxHeight + (parseInt(style.paddingTop, 10) || 0) + (parseInt(style.paddingBottom, 10) || 0);

            if (categoryContainer.clientHeight < maxHeight) {
                categoryContainer.style.height = `${maxHeight}px`;
            }
        }
    }
}

/**
 * Function that handles window resizes so that category container height can be dynamically re-calculated
 */
function onWindowResize() {
    recalculateCategoryContainerHeight(document.getElementById(navId));
}

/**
 * The behavior to run on a scroll
 * - Toggling the sticky class on the headerBlock
 */
export function onScroll(): void {
    const lastKnownScrollPosition = window.scrollY;
    const header = document.querySelector('.' + headerBlock);

    if (header) {
        const navHeight = document.querySelector('.cms-header__top-nav').clientHeight;
        header.classList.toggle(classNames.isSticky, lastKnownScrollPosition > navHeight);
    }
}

/**
 * The behavior to run on click on the mobile navigation toggle
 * @param evt - the MouseClickEvent on the toggle
 */
function toggleMobileMenu(evt: Event): void {
    evt.preventDefault();

    const navToggle = evt.target as HTMLElement;

    navToggle.classList.toggle(classNames.isActive);

    const mainNav = document.getElementById(navId);
    mainNav.classList.toggle(classNames.isOpen);

    document.removeEventListener('click', windowClickCloseMenu);

    if (navToggle.classList.contains(classNames.isActive)) {
        // Open all items under "||| Menu"
        openHamburgerMenuItems(mainNav);
        document.addEventListener('click', windowClickCloseMenu);
    } else {
        // Clear all is-open and is-hidden-sm classes on the menu items
        closeMenu(mainNav);
        clearMobileIsHiddenState(mainNav);
    }
}

/**
 * Closes menu and all child sub-menus
 * @param mainNav Main navigation menu
 */
function closeMenu(mainNav: HTMLElement): void {
    mainNav.querySelectorAll(`.${classNames.isOpen}`).forEach((elem: HTMLElement) => {
        elem.classList.remove(classNames.isOpen);
    });

    mainNav.classList.remove(classNames.isOpen);
}

/**
 * Toggle all nav items under "||| Menu" is-open state
 * @param mainNav Highiest level main-nav element
 */
function openHamburgerMenuItems(mainNav: HTMLElement): void {
    const menuElements = mainNav.getElementsByClassName(jsNavMenuClass);

    if (menuElements && menuElements.length === 1) {
        const menu = menuElements[0];
        menu.classList.add(classNames.isOpen);
        firstChildOf(menu, bemNavSubmenuClass).classList.add(classNames.isOpen);
    }
}
