import { firstChildOf } from '../../helpers/selectors';

const searchBarClass = 'search-bar';
const searchBarButton = `${searchBarClass}__btn`;
const searchBarInput = `${searchBarClass}__input`;
const toggleableSearchBarClass = `${searchBarClass}--toggleable`;

export const submitClassName = 'submit';

/**
 * Initializes toggleable search bars
 */
export function initToggleableSearchBars(): void {
    Array.from(document.getElementsByClassName(toggleableSearchBarClass)).forEach(form => {
        const button = firstChildOf(form, searchBarButton);
        const inputField = firstChildOf(button, searchBarInput);

        inputField.addEventListener('focus', () => button.classList.add(submitClassName));
        inputField.addEventListener('blur', () => button.classList.remove(submitClassName));
    });
}
